import React  from 'react';
import axios from "axios";
import Movie from "./Movie";
import "./App.css";


class App extends React.Component {
  state = {
    isLoading: true,
    movies: []
  }
  getMovies = async() => {
    const {data: {data: {movies}}} = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");  //data.data.movies
    //this.setState({movies:movies})       //앞 movies는 (set)State의 movies, 뒤 movies는 axios에서 온 movies
    this.setState({movies, isLoading: false})      //윗줄 이렇게 줄이기 가능
  }  
  componentDidMount() {
    this.getMovies();
  }
  render() {
    const { isLoading, movies } = this.state;
    return (
      <div>
        <section className="container">
          {isLoading ? (
            <div className="loader">
              <span className="loader_text">Loading...</span>
            </div>
          ) : (
            <div className="movies">
              {movies.map(movie => (
                <Movie 
                  key={movie.id} 
                  id={movie.id} 
                  year={movie.year} 
                  title={movie.title} 
                  summary={movie.summary} 
                  poster={movie.medium_cover_image} 
                  genres={movie.genres}
                  />
                ))}
            </div>)}
        </section>
      </div>
    ) /* this.state.isLoading */
  }
}

export default App;
